import React, { Component, Dimensions } from 'react';
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavItem,
  MDBFooter,
  MDBNavLink,
  MDBTooltip,
  MDBIcon,
  MDBRow,
  MDBCol,
  MDBContainer,
  MDBListGroup,
  MDBListGroupItem,
  MDBBtn,
  MDBAnimation
} from 'mdbreact';
import { ReactComponent as Logo } from './assets/logo.svg';
import { BrowserRouter as Router } from 'react-router-dom';
import Routes from './Routes';
import profileFoto from './assets/img/rafael.jpeg';

import "./App.css";




class App extends Component {
  state = {
    collapseID: '',
    widthChange: '15rem',
    showCollapseMenu: false,
    mainContentClass: "mainContent",
    sidebarNavClass: "sidebarNav",
  };
  MenuLinks = () => {
    const { collapseID } = this.state;
    return (
      <MDBContainer>
        <MDBNavbarNav>
          <MDBNavItem>
            <MDBNavLink
              exact
              to='/'
              onClick={() => this.closeCollapseNav()}
            >
              <strong style={{ color: 'white', marginLeft: '1rem' }}>Página inicial</strong>
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink
              onClick={() => this.closeCollapseNav()}
              to='/sobre'
            >
              <strong style={{ color: 'white', marginLeft: '1rem' }}>Sobre</strong>
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink
              onClick={() => this.closeCollapseNav()}
              to='/portifolio'
            >
              <strong style={{ color: 'white', marginLeft: '1rem' }}>Portifólio</strong>
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink
              onClick={() => this.closeCollapseNav()}
              to='/cv'
            >
              <strong style={{ color: 'white', marginLeft: '1rem' }}>Meu CV</strong>
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink
              onClick={() => this.closeCollapseNav()}
              to='/contato'
            >
              <strong style={{ color: 'white', marginLeft: '1rem' }}>Contato</strong>
            </MDBNavLink>
          </MDBNavItem>

        </MDBNavbarNav>
      </MDBContainer>

    );
  };

  getOpenedCollapseNav = () => {
    if (this.state.sidebarNavClass == "sidebarNav") {
      this.setState({ sidebarNavClass: "sidebarNavOpenned" });
    } else {
      this.setState({ sidebarNavClass: "sidebarNav" });
    }
  };

  closeCollapseNav = () => {
    this.setState({ sidebarNavClass: "sidebarNav" });
  }

  getCollapseCloseButton = () => {
    if (this.state.sidebarNavClass == "sidebarNavOpenned") {
      return (

        <div style={{ height: 35 }}>
          <div className="float-right">

            <MDBTooltip placement='top' domElement style={{ display: "block" }}>
              <MDBBtn flat
                onClick={() => this.closeCollapseNav()}
              >
                <MDBIcon fas icon="times" className="mr-2 white-text" style={{ fontSize: 25 }} />
              </MDBBtn>
              <span>Fechar</span>
            </MDBTooltip>
          </div>
        </div>
      )
    }
  }

  render() {
    const { collapseID, widthChange, showCollapseMenu, mainContentClass, sidebarNavClass } = this.state;
    return (
      <Router>
        <div className='flyout'>
          <div className={sidebarNavClass}>
            <MDBAnimation type="zoomIn" duration="500ms">
              {this.getCollapseCloseButton()}
              <MDBRow style={{ marginTop: '2rem', marginBottom: '2rem' }}>
                <MDBCol>
                  <img href='/' src={profileFoto} className="rounded mx-auto d-block" alt="aligment" />
                </MDBCol>
              </MDBRow>
              {this.MenuLinks()}
            </MDBAnimation>
          </div>

          <main className={mainContentClass}>
            <MDBNavbar color='black' dark expand='md' fixed='top' className="navbarTop" scrolling>
              <MDBNavbarBrand href='/' className='py-0 font-weight-bold'>
                <MDBIcon fas icon="terminal" className="mr-2" />
                <strong className='align-middle'>Rafael Guimarães</strong>
              </MDBNavbarBrand>
              <MDBNavbarToggler
                onClick={() => this.getOpenedCollapseNav()}
              />
              <MDBCollapse
                id='mainNavbarCollapse'
                isOpen={this.state.collapseID}
                navbar
              >
                <MDBNavbarNav right>
                  <MDBNavItem>
                    <MDBNavLink
                      exact
                      to='/'
                      onClick={() => this.closeCollapseNav()}
                    >
                      <MDBIcon icon="home" />
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBTooltip placement='bottom' domElement style={{ display: "block" }}>
                      <a
                        className='nav-link Ripple-parent'
                        href='https://github.com/rafaelgcs'
                        rel="Github Rafaelgcs"
                      >
                        <strong><MDBIcon fab icon="github" /></strong>
                      </a>
                      <span>GitHub rafaelgcs</span>
                    </MDBTooltip>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBTooltip placement='bottom' domElement style={{ display: "block" }}>
                      <a
                        className='nav-link Ripple-parent'
                        href='https://instagram.com/dev.rafael'
                        target='_blank'
                        rel="@dev.rafael - instagram"
                      >
                        <strong><MDBIcon fab icon="instagram" /></strong>
                      </a>
                      <span>@dev.rafael</span>
                    </MDBTooltip>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBTooltip placement='bottom' domElement style={{ display: "block" }}>
                      <a
                        className='nav-link Ripple-parent'
                        href='https://linkedin.com/in/rafaelgcs'
                        target='_blank'
                        rel="rafaelgcs - LinkedIn"
                      >
                        <strong><MDBIcon fab icon="linkedin-in" /></strong>
                      </a>
                      <span>LinkedIn</span>
                    </MDBTooltip>
                  </MDBNavItem>
                </MDBNavbarNav>
              </MDBCollapse>
            </MDBNavbar>
            <Routes />
            <MDBFooter color='dark-grey'>
              <p className='footer-copyright mb-0 py-3 text-center'>
                &copy; {new Date().getFullYear()} Copyright:
              <a href='/'> Rafael Guimarães </a>
              </p>
            </MDBFooter>
          </main>

        </div>
      </Router>
    );
  }
}

export default App;
